  .input_control {
    height: 100%;
    display: flex;
    /* justify-content: center; */
    /* align-items: center; */
  }
  
  .textfield_align {
    position: relative;
  }
  
  .label_style {
    position: absolute;
    font-size: 0.8rem;
    left: 0;
    /* top: 50%; */
    transform: translateY(-50%);
    background-color: white;
    color: gray;
    padding: 0 0.3rem;
    margin: 0 0.5rem;
    transition: .1s ease-out;
    transform-origin: left top;
    pointer-events: none;
    
  }
  .num_field {
    font-size: 1rem;
    outline: none;
    border: 1px solid gray;
    border-radius: 0px 5px 5px 0px;  
    padding: 1rem 0.7rem;
    color: gray;
    transition: 0.1s ease-out;
    height: 42px;
    font-style: italic;
    width: 202px;
  }
  .num_field:focus {
    border-color: #6200EE;  
  }
  .num_field:focus + label {
    color: #6200EE;
    top: 0;
    transform: translateY(-50%) scale(.9);
  }
  .num_field:not(:placeholder-shown) + label {
    top: 0;
    transform: translateY(-50%) scale(.9);
  }

  .country_field {
    font-size: 1rem;
    outline: none;
    border: 1px solid gray;
    border-radius: 5px 0px 0px 5px;  
    padding: 0.5rem 0.7rem;
    color: gray;
    transition: 0.1s ease-out;
    width: 58px;
    height: 42px;
    
  }
  .country_field:focus {
    border-color: #6200EE;  
  }
  .country_field:focus + label {
    color: #6200EE;
    top: 0;
    transform: translateY(-50%) scale(.9);
  }
  .country_field:not(:placeholder-shown) + label {
    top: 0;
    transform: translateY(-50%) scale(.9);
  }
  
  .accordion-collapse {
    border: none !important;
   }
   .accordion-button {
    padding: 0px;
    border: none !important;
    text-align: left;
    font-size: 21px;
    font-weight: 700;
    color: #0d55cf !important;
    /* padding-top: 10px; */
    margin-top: 30px;
   }
   .accordion-button:focus {
    box-shadow: none !important;
    border: none !important;
   }
   .accordion-button:not(.collapsed) {
    background: none;
    color: #000000 !important;
    opacity: 0.6;
   }
   .accordion-body {
    padding: 15px;
    background-color: #f6f6f6;
   }
   .accordion-button::before {
    width: auto;
    height: auto;
    content: "+";
    font-size: 40px;
    background-image: none !important;
    font-weight: 100;
    color: #000000 !important;
    opacity: 0.6;
    transform: translateY(-4px);
    padding-right: 10px;
   }
   .accordion-button:not(.collapsed)::before {
    width: auto;
    height: auto;
    background-image: none !important;
    content: "-";
    font-size: 48px;
    transform: translate(-5px, -4px);
    transform: rotate(0deg);
    padding-right: 10px;
   }
   .accordion-button::after {
    background-image: none !important;
   }
   .accordion-button:not(.collapsed)::after {
    background-image: none !important;
   }
   .accordion-item {
    border: none !important;
   }
   